import React from 'react';
import { connect } from 'react-redux';
import Row from '../Row';
class Matrix extends React.Component {
  render () {
    let { matrix } = this.props;
    const { level, mediaMatrix } = this.props;
    matrix = mediaMatrix || matrix;
    // Âü∫Á°ÄÂ§ßÂ∞è‰∏∫16px * 2em * 15‰∏™ = 480px
    // Áî±Ê≠§ËÆ°ÁÆóÂá∫count‰∏∫ 8 15 16 20Êó∂ fontSizeÂàÜÂà´‰∏∫30 16 15 12
    return (
      <div className="matrix" style={{fontSize: [30, 16, 15, 12][level] + 'px'}}>
        {
          matrix.map((row, index) => {
            return (
              <Row row={ row } key={ index }></Row>
            )
          })
        }
      </div>
    )
  }
}

// ‰ªéstate‰∏≠Êò†Â∞ÑmatrixÂà∞MatrixÁªÑ‰ª∂
const mapStateToProps = (state) => {
  return {
    mediaMatrix: state.mediaMatrix,
    matrix: state.matrix,
    level: state.level
  }
}

export default connect(mapStateToProps)(Matrix)
